.aboutus-footer {
  background: #0a2540;
  color: #fff;
  padding: 2rem 0 1rem 0;
  text-align: center;
  width: 100%;
  margin-top: 2rem;
}
.aboutus-footer-content {
  max-width: 700px;
  margin: 0 auto;
  padding: 0 1rem;
}
.aboutus-footer h3 {
  margin-bottom: 0.5rem;
  font-size: 1.5rem;
  color: #10b3b3;
}
.aboutus-footer p {
  margin-bottom: 1rem;
  font-size: 1.1rem;
  line-height: 1.6;
}
.aboutus-footer-contact {
  margin-bottom: 0.5rem;
  font-size: 1rem;
}
.aboutus-footer-copy {
  font-size: 0.95rem;
  color: #b6d4f7;
}
.aboutus-footer-link {
  color: #10b3b3;
  text-decoration: none;
  font-weight: 600;
  border: 1px solid #10b3b3;
  border-radius: 4px;
  padding: 0.4em 1em;
  background: transparent;
  transition: background 0.2s, color 0.2s;
  margin-top: 0.5rem;
  display: inline-block;
}
.aboutus-footer-link:hover, .aboutus-footer-link:focus {
  background: #10b3b3;
  color: #fff;
  text-decoration: none;
}
@media (max-width: 600px) {
  .aboutus-footer-content {
    padding: 0 0.5rem;
  }
  .aboutus-footer h3 {
    font-size: 1.2rem;
  }
  .aboutus-footer p, .aboutus-footer-contact, .aboutus-footer-copy {
    font-size: 0.95rem;
  }
} 