.notification-card {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 1.5rem;
  border-radius: 8px;
  margin-bottom: 1.5rem;
  font-weight: 500;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
  cursor: pointer;
  outline: none;
  transition: box-shadow 0.2s;
}
.notification-card:focus {
  box-shadow: 0 0 0 2px #10b3b3;
}
.notification-icon {
  font-size: 1.7rem;
  display: flex;
  align-items: center;
}
.notification-content {
  display: flex;
  flex-direction: column;
}
.notification-title {
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 0.2rem;
}
.notification-message {
  font-size: 1rem;
}
.notification-info {
  background: #e6f7ff;
  border: 1px solid #91d5ff;
  color: #096dd9;
}
.notification-warning {
  background: #fffbe6;
  border: 1px solid #ffe58f;
  color: #ad8b00;
}
.notification-success {
  background: #f6ffed;
  border: 1px solid #b7eb8f;
  color: #389e0d;
}
.notification-error {
  background: #fff2f0;
  border: 1px solid #ffccc7;
  color: #cf1322;
} 