.activity-log {
  max-width: 900px;
  margin: 2rem auto;
  padding: 2rem;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.07);
}
.activity-log h1 {
  margin-bottom: 1.5rem;
  font-size: 2rem;
  color: #234567;
}
.activity-log-filters {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.5rem;
  flex-wrap: wrap;
}
.activity-log-filters input {
  padding: 0.5rem 1rem;
  border: 1px solid #d9d9d9;
  border-radius: 6px;
  font-size: 1rem;
  min-width: 180px;
}
.activity-log-table {
  width: 100%;
  border-collapse: collapse;
  background: #fafcff;
}
.activity-log-table th, .activity-log-table td {
  padding: 0.75rem 1rem;
  border-bottom: 1px solid #e6e6e6;
  text-align: left;
}
.activity-log-table th {
  background: #f0f5ff;
  color: #234567;
  font-weight: 600;
}
.activity-log-table tr:last-child td {
  border-bottom: none;
}
.error {
  color: #cf1322;
  background: #fff2f0;
  border: 1px solid #ffccc7;
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 1rem;
} 