body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f8fafd;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.header {
  background-color: #e3f0fa;
  padding: 16px 32px;
  box-shadow: 0 2px 8px rgba(0, 123, 255, 0.04);
  color: #007bff;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.sidebar {
  background-color: #f0f4f8;
  color: #234567;
  padding: 20px 16px;
  min-width: 220px;
  min-height: 100vh;
  box-shadow: 2px 0 8px rgba(0,0,0,0.03);
}

.main-content {
  padding: 24px;
}

.footer {
  text-align: center;
  padding: 10px 0;
  background-color: #e3f0fa;
  color: #007bff;
}

.button {
  padding: 10px 20px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1rem;
  transition: background 0.2s, color 0.2s, box-shadow 0.2s;
  outline: none;
}

.button-primary {
  background-color: #007bff;
  color: white;
}
.button-primary:hover, .button-primary:focus {
  background-color: #005fa3;
  box-shadow: 0 2px 8px rgba(0,123,255,0.08);
}

.button-secondary {
  background-color: #e3f0fa;
  color: #007bff;
}
.button-secondary:hover, .button-secondary:focus {
  background-color: #b6d4f7;
  color: #005fa3;
}

.input {
  padding: 10px;
  border: 1px solid #b6d4f7;
  border-radius: 5px;
  width: 100%;
  box-sizing: border-box;
  font-size: 1rem;
}
.input:focus {
  border-color: #007bff;
  outline: 2px solid #b6d4f7;
}

.card {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
  padding: 2rem;
  margin-bottom: 2rem;
}

.modal {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.12);
  padding: 2rem;
  max-width: 500px;
  margin: 2rem auto;
}

.error {
  color: #d32f2f;
  font-size: 0.95em;
  margin-top: 0.5em;
}

/* Shared Table Styles for Dashboards */
.analytics-table, .activity-log-table, .patient-appointments-table, .doctor-appointments-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 1rem;
  background: #fff;
  border-radius: 8px;
  overflow: hidden;
  margin-bottom: 1.5rem;
}
.analytics-table th, .analytics-table td,
.activity-log-table th, .activity-log-table td,
.patient-appointments-table th, .patient-appointments-table td,
.doctor-appointments-table th, .doctor-appointments-table td {
  padding: 0.75rem 0.5rem;
  border: 1px solid #e0e0e0;
  text-align: left;
}
.analytics-table th, .activity-log-table th,
.patient-appointments-table th, .doctor-appointments-table th {
  background: #f0f8ff;
  color: #234567;
  font-weight: 600;
}
.analytics-table tbody tr:nth-child(even),
.activity-log-table tbody tr:nth-child(even),
.patient-appointments-table tbody tr:nth-child(even),
.doctor-appointments-table tbody tr:nth-child(even) {
  background: #f7fbff;
}
.analytics-table tbody tr:hover,
.activity-log-table tbody tr:hover,
.patient-appointments-table tbody tr:hover,
.doctor-appointments-table tbody tr:hover {
  background: #e6f7ff;
  transition: background 0.2s;
}
@media (max-width: 800px) {
  .analytics-table, .activity-log-table, .patient-appointments-table, .doctor-appointments-table {
    font-size: 0.95rem;
    display: block;
    overflow-x: auto;
    white-space: nowrap;
  }
}

/* Empty/Error State Styles */
.table-empty-state, .table-error-state {
  text-align: center;
  color: #888;
  padding: 2rem 0;
  font-size: 1.1rem;
}
.table-empty-state svg, .table-error-state svg {
  display: block;
  margin: 0 auto 0.5rem auto;
  color: #b6d4f7;
  font-size: 2.5rem;
}