.doctor-dashboard {
  padding: 2rem;
  background: #f8fafd;
  min-height: 100vh;
}
.doctor-dashboard h1 {
  color: #10b3b3;
  font-size: 2.2rem;
  font-weight: 700;
  margin-bottom: 2rem;
}
.doctor-dashboard h2 {
  color: #234567;
  font-size: 1.3rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
}
.doctor-dashboard table {
  width: 100%;
  border-collapse: collapse;
  background: #fff;
  border-radius: 8px;
  overflow: hidden;
  margin-bottom: 2rem;
}
.doctor-dashboard th, .doctor-dashboard td {
  padding: 0.9rem 1rem;
  border-bottom: 1px solid #e0e0e0;
  text-align: left;
}
.doctor-dashboard th {
  background: #e6f7ff;
  color: #234567;
  font-weight: 600;
}
.doctor-dashboard tr:last-child td {
  border-bottom: none;
}
.doctor-dashboard .error {
  color: #d32f2f;
  font-size: 1rem;
  margin-bottom: 1rem;
  text-align: center;
}
.doctor-dashboard .appointments {
  margin-top: 2rem;
}
@media (max-width: 900px) {
  .doctor-dashboard {
    padding: 1rem;
  }
}
@media (max-width: 600px) {
  .doctor-dashboard h1 {
    font-size: 1.3rem;
  }
  .doctor-dashboard h2 {
    font-size: 1rem;
  }
  .doctor-dashboard th, .doctor-dashboard td {
    padding: 0.5rem 0.5rem;
  }
}